<template>
  <v-app>
    <v-container
      class="primary confirm-register"
      fluid
      fill-height
    >
      <div class="confirm-register-box text-center">
        <h1 v-if="error">
          {{ error }}
        </h1>

        <h1 v-if="success">
          {{ success }}
        </h1>

        <btn-secondary
          v-if="success"
          class="confirm-register-btn mt-4 accent font-weight-bold"
          text="Fazer Login"
          :block="false"
          @clicked="goToLogin"
        />
      </div>
    </v-container>
  </v-app>
</template>

<script>
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'

import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  name: 'ConfirmRegister',
  components: { BtnSecondary },
  mixins: [ NavigationMixin ],
  props: {
    success: {
      default: null
    },
    error: {
      default: null
    },
    isBarber: { default: false }
  },
  methods: {
    goToLogin () {
      if (this.isBarber) this.goTo('login-barber')
      else this.goTo('login')
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';

.confirm-register { position: relative;
  .confirm-register-box {
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: fit-content;
    width: fit-content;
    h1 { color: #fff !important; margin: 0; font-size: 22px; }
    .confirm-register-btn { color: $primaryDark; }
  }
}
</style>